






import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

import G2 from '@antv/g2';
// import * as G2 from '@antv/g2/src';

@Component({
})
export default class G2MiniBar extends Vue {

    @Prop({type: String, default: '#1890FF'})
    private color!: string;

    @Prop({type: Number, default: 0})
    private height!: number;

    @Prop({type: Number, default: 5})
    private borderWidth!: number;

    @Prop({default: [8, 8, 8, 8]})
    private padding!: number[];

    @Prop({type: Array, default: [ ]})
    private data!: Array<{ x: number; y: number; [key: string]: any }>;

    @Prop({type:String, default: ''})
    private yTooltipSuffix!: string;

    private node: any ;

    private chart: any;

    constructor() {
        super();
    }

    private install() {
        if (!this.data || (this.data && this.data.length < 1)) {
            return;
        }

        this.node = this.$refs.container;

        this.node.innerHTML = '';
        const chart = new G2.Chart({
            container: this.node,
            forceFit: true,
            height: +this.height,
            padding: this.padding,
            legend: null,
        });
        chart.axis(false);
        chart.source(this.data, {
            x: {
                type: 'cat',
                },
            y: {
                    min: 0,
                },
        });
        chart.tooltip({
            'showTitle': false,
            'hideMarkers': false,
            'crosshairs': false,
            'g2-tooltip': { padding: 4 },
            'g2-tooltip-list-item': { margin: `0px 4px` },
        });
        chart
            .interval()
            .position('x*y')
            .size(this.borderWidth)
            .color(this.color)
            .tooltip('x*y', (x: any, y: any) => {
                return {
                    name: x,
                    value: y + this.yTooltipSuffix,
                    };
        });
        chart.render();
        this.chart = chart;
    }

    private uninstall() {
        if (this.chart) {
            this.chart.destroy();
            this.chart = null;
        }
    }

    private mounted() {
        this.install();
    }

    private destroyed() {
        this.uninstall();
    }

    private updated(){
        console.log('updated');
    }

    @Watch('data')
    private onDataChanged(
        val: Array<{ x: number; y: number; [key: string]: any }>,
        oldVal: Array<{ x: number; y: number; [key: string]: any }>) {
        this.uninstall();
        this.install();
    }

}
