import AdminLayout from '@/layouts/default/AdminLayout.vue';

const router = {
    path: '/list',
    component: AdminLayout,
    children: [{
      path: 'baselist',
      name: 'baselist',
      component: () => import(/* webpackChunkName: "list" */ '@/views/list/BaseList.vue'),
      meta: {
        title: 'baselist',
      },
    },
    {
      path: 'cardlist',
      name: 'cardlist',
      component: () => import(/* webpackChunkName: "list" */ '@/views/list/CardList.vue'),
      meta: {
        title: 'cardlist',
      },
    },
    ],
  };

export default router;
