import AdminLayout from '@/layouts/default/AdminLayout.vue';

const router = {
    path: '/style',
    component: AdminLayout,
    children: [{
      path: 'typography',
      name: 'typography',
      component: () => import(/* webpackChunkName: "style" */ '@/views/style/Typography.vue'),
      meta: {
        title: 'typography',
      },
    },
    {
      path: 'grid',
      name: 'grid',
      component: () => import(/* webpackChunkName: "style" */ '@/views/style/Grid.vue'),
      meta: {
        title: 'grid',
      },
    },
    {
      path: 'colors',
      name: 'colors',
      component: () => import(/* webpackChunkName: "style" */ '@/views/style/Colors.vue'),
      meta: {
        title: 'colors',
      },
    },
    ],
  };

export default router;
