





























import { Component, Prop, Vue } from 'vue-property-decorator';


import * as _ from 'lodash';

import { State, Mutation, Getter, namespace } from 'vuex-class';
const routerTabModule = namespace('routerTab');


import OpenPageTab from '@/components/OpenPageTab.vue';

import AdminHeader from './components/AdminHeader.vue';
import AdminSidebar from './components/AdminSidebar.vue';


@Component({
  components: {
    AdminHeader,
    AdminSidebar,
    OpenPageTab,
  },
})
export default class AdminLayout extends Vue {

  @routerTabModule.State('openPageList')
  private pageList!: Array<{}> ;

  @routerTabModule.Mutation('removeOpenPage')
  private removeOpenPage!: Function ;

  @routerTabModule.State('activeName')
  private activeName!: string ;

  @routerTabModule.Getter('closable')
  private tagsClosable!: boolean;

  constructor() {
    super();
  }

  private handlerTabClick(name: string){
    this.$router.push(name);
  }

  private handlerTabRemove(name: string) {

    const oldActiveName = this.activeName;

    this.removeOpenPage({
      name:name
    });

    // æ§å¶æ¯å¦éè¦è·³è½¬
    if(this.activeName === oldActiveName){
      return;
    }
  
    this.$router.push(this.activeName);
  }
}
