






import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

import * as G2 from '@antv/g2';

@Component({})
export default class G2Bar extends Vue {

  @Prop({ type: String, default: '' })
  private title!: string;

  @Prop({ type: String, default: 'rgba(24, 144, 255, 0.85)' })
  private color!: string;

  @Prop({ type: Number, default: 0 })
  private height!: number;

  @Prop({ type: Number, default: 5 })
  private borderWidth!: number;

  @Prop({ type: Array, default: [8, 8, 8, 8] })
  private padding!: number[];

  @Prop({ type: Array, default: [] })
  private data!: Array<{ x: any; y: any; [key: string]: any }>;

  @Prop({ type: Boolean, default: true })
  private autoLabel!: boolean;

  private node: any;

  private chart: any;

  private autoHideXLabels = false;

  constructor() {
    super();
  }

  private install() {
    

    this.node = this.$refs.container;

    
    const canvasWidth = this.node.clientWidth;
    const minWidth = this.data.length * 30;
    if (canvasWidth <= minWidth) {
      if (!this.autoHideXLabels) {
        this.autoHideXLabels = true;
      }
    } else if (this.autoHideXLabels) {
      this.autoHideXLabels = false;
    }

    if (!this.data || (this.data && this.data.length < 1)) {
      return;
    }

    this.node.innerHTML = '';

    const chart = new G2.Chart({
      container: this.node,
      forceFit: true,
      height: +this.height - 22,
      legend: null,
      'padding': this.padding || 'auto',
    });

    chart.axis('x', !this.autoHideXLabels);
    chart.axis('y', {
      title: false,
      line: false,
      tickLine: false,
    });

    chart.source(this.data, {
      x: {
        type: 'cat',
      },
      y: {
        min: 0,
      },
    });

    chart.tooltip({
      showTitle: false,
    });
    chart
      .interval()
      .position('x*y')
      .color(this.color)
      .tooltip('x*y', (x: any, y: any) => {
        return {
          name: x,
          value: y,
        };
      });
    chart.render();
    this.chart = chart;
  }

  private uninstall() {
    if (this.chart) {
      this.chart.destroy();
      this.chart = null;
    }
  }

  private mounted() {
    this.install();
  }

  private destroyed() {
    this.uninstall();
  }

  @Watch('data')
  private onDataChanged(
    val: Array<{ x: number; y: number; [key: string]: any }>,
    oldVal: Array<{ x: number; y: number; [key: string]: any }>,
  ) {
    this.uninstall();
    this.install();
  }
}
