






























import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({})
export default class HeaderUser extends Vue {

    private handleCommand(command: string) {
        switch (command) {
            case 'logout':
                this.logout();
                break;
            default:
                return;
        }
    }

    private logout() {
        // this.$router.push('/passport/login');
    }
}
