






























import { Component, Vue, Inject, Prop, Emit } from 'vue-property-decorator';

@Component({

})
export default class SidebarMenuShrink extends Vue {

    private name: string = 'sidebarMenuShrink';

    @Prop({type: Array})
    private menuList!: Array<any>;

    @Prop({default: 'white'})
    private iconColor!: string;

    @Prop({default: 'darck'})
    private menuTheme!: string;

    get notGroupMenuList() {
        return this.menuList.filter((item) => {
            return !item.isgroup;
        });
    }

    private hasChildren(item: any) {
        return item.children && item.children.length > 0;
    }

    @Emit('on-change')
    private changeMenu(active: string) {
    }
}
