


































































































import { Component, Prop, Vue, Emit, Watch } from 'vue-property-decorator';

@Component
export default class OpenPageTab extends Vue {
    @Prop({type: String})
    private activeName!: string;

    @Prop({type: Boolean, default: false})
    private closable!: boolean;

    @Prop({type: Array, default: []})
    private pages!: Array<{}>;

    get canClose() {
        return !(this.pages.length <= 1);
    }

    private activeColor(item: any) {
        if (item.name === this.activeName ) {
            return 'primary';
        }
        return '';
    }

    @Emit()
    private onTabRemove(name: string) {
    }

    @Emit()
    private onTabClick(name: string) {
    }

    private handleTabRemove(name: string) {
        this.onTabRemove(name);
    }


    private handlerTabClick(name: string){
        this.onTabClick(name);
    }

}
