import * as _ from 'lodash';

const routerTab = {
    namespaced: true,
    state: {
      openPageList: [],
      activeName: '',
    },
    getters:{
      closable: (state:any ) =>{
        return state.openPageList.length > 1; 
      }
    },
    mutations: {
      addOpenPage(state: any, page: any) {
        const oldTables = state.openPageList;
        const targetName = page.name;
        const isPassport = _.startsWith( targetName, '/passport');
        if  ( isPassport === true ) {
          return;
        }

        const exist = _.find(oldTables, ( item: any )  => {
          return item.name === page.name;
        });

        if ( exist == null ) {
          oldTables.push(page);
          state.openPageList = oldTables;
        }

        state.activeName = page.name;

      },
      removeOpenPage(state: any, page: any) {

        // è·¯ç±æ²¡ææ¹å
        const oldTabs = state.openPageList;
        let activeName = state.activeName;
        const targetName = page.name;
        
        if ( activeName === targetName ) {
          _.forEach(oldTabs, (tab: any, index: number) => {
            if (tab.name === targetName) {
              const nextTab = oldTabs[index + 1] || oldTabs[index - 1];
              if (nextTab) {
                activeName = nextTab.name;
              }
            }
          });
        } 

        const newTabs = _.filter(oldTabs, (tab: any) => tab.name !== targetName );

        state.openPageList = newTabs;
        state.activeName = activeName;

      },
    },
    actions: {
    },
  };


export default routerTab;
